@import url('https://fonts.googleapis.com/css?family=Open+Sans:300,400,700&display=swap');

body {    
    background: no-repeat url('./images/icons/tyre.png');
    background-position: 50% 0;
    font-family: Open Sans;
    font-size: 16px;    
    background-color: #ffffff;
}
#root {    
    margin: 0 auto;
    max-width: 600px;
    padding-left: 5px;
}
#menu {
    text-align: center;
    margin: 10px 10px;
}
#addcar, #additem, #editcar{
    display: flex;
    flex-direction: column;
    text-align: center;
}
label {
    display: flex;
    justify-content: space-between;
    margin: 10px 10px;
}
.radio {
    display: flex;
    justify-content: space-between;
    width: 200px;
    margin: 10px auto;
}
.reportList, .reportheadercols {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: center;
}
.reportheadercols {
    font-weight: 700;
}
.reportList div, .reportheadercols div{
    width: 75px;
    height: 25px;
    font-size: 14px;
}
.reportList button img {
    width: 20px;
}
.reportList div:first-child, .reportheadercols div:last-child{
    width: 80px
}
.reportList div:nth-child(3), .reportheadercols div:nth-child(3){
    width: 55px
}
.reportList div:last-child, .reportheadercols div:last-child{
    width: 50px
}
input[type="text"], input[type="number"], input[type="date"], select, input[type="range"] {
    border-radius: 8px;
    width: 200px;
    height: 30px;
    border: 1px solid rgb(17, 0, 255);
    box-sizing:border-box;
    color: #000000;
    background-color: #ffffff;
    padding: 0 10px 0 10px;
    font-size: 16px;
}
button {
    font-size: 16px;
    background-color: rgb(17, 0, 255);
    color: #ffffff;    
    border: none;
    border-radius: 8px;
}
#addcar button, #additem button, #editcar button {
    width: 200px;
    height: 40px;
    margin: 10px auto;
    
}
button:disabled,
button[disabled] {
    color: rgb(95, 94, 94);
    background-color: rgb(17, 0, 255, 0.5);
}
button:hover {
    cursor: pointer;
}
#categorylist {
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
}
#categorylist label {
    display: inline-block;
    text-align: center;
    font-size: 14px;
    margin: 5px 5px;
}
#categorylist label input {
    width: 13px;
    height: 13px;
}
#selectcar {
    display: flex;
    height: 30px;
    margin-left: calc(50% - 100px);
}
#selectcar button, .reportList button {
    background: none;
    width: 30px;
    border: none;
    padding: 0;
    margin: 0 10px;
}
#selectcar button img {
    width: 30px;    
}
#selectcar button:hover{    
    cursor: pointer;
}
#selectcar a img{
    width: 30px;
}
#menu img{
    height: 40px;
    margin: 0 15px;
}
.error {
    color: red;
    height: 25px;
}
#fuelconsumption {
    display: block;
    margin: 0 5px;
}
.fuelconsumptionrow {
    display: flex;    
}
.reportheader {
    font-weight: 700;
    margin-top: 10px;
}

input[type=range] {
    -webkit-appearance: none;
  }
  input[type=range]:focus {
    outline: none;
  }
  input[type=range]::-webkit-slider-runnable-track {
    width: 100%;
    height: 8.4px;
    cursor: pointer;
    box-shadow: 1px 1px 1px #000000, 0px 0px 1px #0d0d0d;
    background: #3071a9;
    border-radius: 1.3px;
    border: 0.2px solid #010101;
  }
  input[type=range]::-webkit-slider-thumb {
    box-shadow: 1px 1px 1px #000000, 0px 0px 1px #0d0d0d;
    border: 1px solid #000000;
    height: 25px;
    width: 10px;
    border-radius: 9px;
    background: #ffffff;
    cursor: pointer;
    -webkit-appearance: none;
    margin-top: -8.5px;
  }
  input[type=range]:focus::-webkit-slider-runnable-track {
    background: #367ebd;
  }
  input[type=range]::-moz-range-track {
    width: 100%;
    height: 8.4px;
    cursor: pointer;
    box-shadow: 1px 1px 1px #000000, 0px 0px 1px #0d0d0d;
    background: #3071a9;
    border-radius: 1.3px;
    border: 0.2px solid #010101;
  }
  input[type=range]::-moz-range-thumb {
    box-shadow: 1px 1px 1px #000000, 0px 0px 1px #0d0d0d;
    border: 1px solid #000000;
    height: 25px;
    width: 10px;
    border-radius: 9px;
    background: #ffffff;
    cursor: pointer;
  }
  input[type=range]::-ms-track {
    width: 100%;
    height: 8.4px;
    cursor: pointer;
    background: transparent;
    border-color: transparent;
    color: transparent;
  }
  input[type=range]::-ms-fill-lower {
    background: #2a6495;
    border: 0.2px solid #010101;
    border-radius: 2.6px;
    box-shadow: 1px 1px 1px #000000, 0px 0px 1px #0d0d0d;
  }
  input[type=range]::-ms-fill-upper {
    background: #3071a9;
    border: 0.2px solid #010101;
    border-radius: 2.6px;
    box-shadow: 1px 1px 1px #000000, 0px 0px 1px #0d0d0d;
  }
  input[type=range]::-ms-thumb {
    box-shadow: 1px 1px 1px #000000, 0px 0px 1px #0d0d0d;
    border: 1px solid #000000;
    height: 25px;
    width: 10px;
    border-radius: 9px;
    background: #ffffff;
    cursor: pointer;
    height: 8.4px;
  }
  input[type=range]:focus::-ms-fill-lower {
    background: #3071a9;
  }
  input[type=range]:focus::-ms-fill-upper {
    background: #367ebd;
  }
  

img:hover {
    margin-top: -5px;
    transition: 0.5s;
}
#period {
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
    margin: 10px 10px;
}
#period input {
    margin: 0 15px 0 5px;
    width: 170px;
}
#fuelconsumption {
    position: absolute;
    top: 0;
    left: calc(50% - 182px);    
}
#fuelconsumption div {
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
    padding-left: 10px;
}
#refueling, #other-costs {
    display: flex;
    flex-direction: column;
    align-items: center;
    position: relative;
}
#refueling {    
    padding-top: 60px;
}
/*.reportList, .reportheadercols, #categorylist {
    display: none;
}*/
 

@media (max-width: 475px) {
    #period {
        flex-direction: column;
        align-content: center;
    }
    input, select {
        width: 100px;
    }
    #selectcar {
        margin-left: calc(50% - 50px);
    }
    #selectcar button img {
        width: 20px;
    }
    #selectcar a img {
        width: 20px;        
    }
    label {
        flex-direction: column;
        align-items: center;
    }
}

@media (max-width: 350px) {
    #refueling {
        padding-top: 80px;
    }
}